@import url(https://fonts.googleapis.com/css?family=Poppins:100,200,300,400,500,600,700,800,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway:400,500&display=swap);
body {
  margin: 0;
  font-family: Poppins, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.font-family-raleway {
  font-family: Raleway, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.bg-info-danger {
  background-image: linear-gradient(to right, var(--info), var(--danger));
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

:root {
    --header-height: 91px;
}

.Home .carousel-home {
    height: calc(100vh - 91px);
    height: calc(100vh - var(--header-height));
}

.ImageCard {
    position: relative;
    overflow: hidden;
}

.ImageCard .inner img {
    z-index: 0;
    transition: .25s;
}

.ImageCard .inner:hover img {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.ImageCard .collections-text {
    position: absolute;
    z-index: 1;
    left: 30px;
    bottom: 70px;
    text-shadow: 0px 0px 5px black;
}

.ImageCard .inner-text {
    position: absolute;
    z-index: 1;
    left: 30px;
    bottom: 20px;
    text-shadow: 0px 0px 5px black;
}




.SideDrawer nav.navbar {
    max-height: calc(100vh - 140px);
	overflow-y: auto;
	scrollbar-color: var(--danger);
	scrollbar-width: 3px;
}

.SideDrawer nav.navbar::-webkit-scrollbar-track
{
	box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #F5F5F5;
}

.SideDrawer nav.navbar::-webkit-scrollbar
{
	width: 3px;
	background-color: #F5F5F5;
}

.SideDrawer nav.navbar::-webkit-scrollbar-thumb
{
	background-color: var(--danger);
}
